<script>
export default {
    name: 'ProgressBar',
    functional: true,
    props: {
        target: {
            type: Number,
            default: 1000,
        },
        current: {
            type: Number,
            default: 500,
        },
        size: {
            type: String,
            default: 'md',
            validator(value) {
                return ['sm', 'md'].includes(value);
            },
        },
        maxWidth: {
            type: String,
            default: '100%',
        },
    },
    render(h, { data, props, scopedSlots: slots }) {
        /**   */
        const percent = (props.current / props.target) * 100;
        /**    scoped slot */
        const params = {
            current: props.current,
            target: props.target,
            percent,
        };
        /**      */
        const { complete, inprocess } = slots;

        /**       */
        const completeText = complete
            ? complete({ params })
            : `  ${params.target} .`;

        /**     */
        const inprocessText = inprocess
            ? inprocess({ params })
            : ` ${params.current}  ${params.target} .`;

        /**   */
        const text = percent >= 100 ? completeText : inprocessText;

        return (
            <div
                style={`max-width: ${props.maxWidth};`}
                class={[
                    `spui-ProgressBar spui-ProgressBar_${props.size}`,
                    data.class,
                    data.staticClass,
                ]}
            >
                <div
                    style={`width: ${percent}%;`}
                    class="spui-ProgressBar__progress"
                ></div>
                <div class="spui-ProgressBar__text">{text}</div>
            </div>
        );
    },
};
</script>

<style lang="scss" scoped>
@import './ProgressBar.scss';
</style>
